.modal {
  background: rgba(0, 0, 0, 0.25);
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center; }
  .modal .content {
    background: white;
    padding: 3rem;
    width: 300px;
    min-width: 200px;
    height: auto;
    font-size: 1.2rem; }
    .modal .content .flist {
      padding: 10px;
      max-height: 200px;
      overflow: auto; }
      .modal .content .flist .img {
        background: #dee2e6;
        border-radius: 50%;
        width: 60px;
        height: 60px;
        object-fit: contain;
        margin: 5px;
        margin-bottom: -5px; }
      .modal .content .flist .nick {
        width: 100%;
        height: 60px;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center; }
        @media (max-width: 679px) {
          .modal .content .flist .nick {
            font-size: 1rem; } }
    .modal .content .close {
      -webkit-transition: 0.3s;
      transition: 0.3s;
      padding: 3px;
      background: #228be6;
      cursor: pointer;
      font-size: 1rem;
      color: #fff;
      width: 70px;
      border-radius: 5%;
      border: white; }
      .modal .content .close:hover {
        background-color: #1971c2; }
  .modal .each-user {
    cursor: pointer;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    width: 100%;
    border-radius: 20px; }
    .modal .each-user:hover {
      background: #dee2e6; }

.show-profile {
  color: #228be6;
  height: 70px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center; }
  .show-profile:hover {
    color: #1864ab; }
