.feed {
  padding: 2px; }
  .feed .ht {
    color: #228be6;
    font-weight: 600; }
  .feed .list {
    box-sizing: border-box;
    width: 100%;
    height: calc(100vh - 84px);
    overflow-y: scroll;
    overflow-x: hidden; }
    @media (max-width: 679px) {
      .feed .list {
        padding-top: 0; } }

.loading {
  height: calc(100vh - 80px); }

.greeting {
  font-size: 1.5rem;
  margin-top: -40px;
  margin-bottom: 20px; }
  @media (max-width: 679px) {
    .greeting {
      font-size: 1.25rem; } }

.following-rec {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center; }
  .following-rec .each-rec {
    cursor: pointer;
    margin: 20px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    background: #dee2e6;
    min-width: 200px;
    border-radius: 20%;
    align-items: center; }
    @media (max-width: 679px) {
      .following-rec .each-rec {
        min-width: 250px;
        min-height: 250px; } }
    .following-rec .each-rec:hover {
      background: #adb5bd;
      -webkit-transition: 0.3s;
      transition: 0.3s; }
    .following-rec .each-rec .profile-pic {
      background: #adb5bd;
      width: 100px;
      height: 100px;
      border-radius: 50%; }
      @media (max-width: 679px) {
        .following-rec .each-rec .profile-pic {
          min-width: 200px;
          min-height: 200px; } }
      .following-rec .each-rec .profile-pic img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        border-radius: 50%; }
    .following-rec .each-rec .nick {
      font-weight: 800;
      margin-top: 5px; }
      @media (max-width: 679px) {
        .following-rec .each-rec .nick {
          font-weight: 800;
          font-size: 1.5rem; } }

.searched-hashtag {
  margin-top: -100px;
  font-size: 1.8rem;
  font-weight: 500;
  color: #495057;
  margin-top: 15px;
  margin-bottom: 15px; }
