.header {
  background: #343a40;
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 80px;
  width: 100%;
  color: white;
  font-weight: 600;
  font-size: 2rem;
  box-shadow: 5px 5px 39px -9px #000000; }
  @media (max-width: 399px) {
    .header {
      font-size: 1.7rem; } }
  @media (max-width: 679px) {
    .header .search-container {
      display: none; } }
  .header .search {
    height: 40px;
    width: 300px;
    border: white;
    border-radius: 3%;
    margin: 10px;
    background: #495057;
    color: white;
    text-align: center;
    font-weight: 700;
    font-size: 1.5rem; }
    @media (max-width: 679px) {
      .header .search {
        width: 200px; } }
    .header .search:focus {
      outline: none;
      -webkit-transition: 0.3s;
      transition: 0.3s;
      background: #868e96; }
  .header .right-part {
    width: 180px;
    display: flex;
    align-items: center;
    justify-content: space-around; }
    @media (max-width: 399px) {
      .header .right-part {
        width: 160px;
        justify-content: space-between; } }
    .header .right-part .mobile-search-icon {
      display: none;
      margin-top: 10px;
      margin-left: 10px; }
      @media (max-width: 679px) {
        .header .right-part .mobile-search-icon {
          display: inline;
          display: initial; } }
