.post-template {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
  overflow-x: hidden; }
  .post-template .nick {
    font-size: 1.8rem;
    font-weight: 800; }
  .post-template .pic {
    background: white;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
    height: 80%;
    width: 50%; }
    .post-template .pic img {
      width: 100%;
      height: 100%;
      object-fit: contain; }
  .post-template .right {
    height: 80%;
    background: #ECEFEE;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23); }
    @media (max-width: 1023px) {
      .post-template .right {
        height: 1000px;
        width: 100%; } }
    .post-template .right .content {
      height: 30%;
      width: 400px;
      overflow: auto;
      background: white;
      padding: 20px;
      font-size: 1.3rem;
      font-weight: 800;
      overflow: scroll;
      overflow-x: hidden;
      overflow-y: auto; }
      @media (max-width: 1023px) {
        .post-template .right .content {
          height: auto;
          width: 95%; } }
      .post-template .right .content .nick-with-delete-button {
        display: flex;
        justify-content: space-between;
        align-items: center; }
        .post-template .right .content .nick-with-delete-button .delete-button {
          border: none;
          outline: none;
          background: #fa5252;
          padding: 8px;
          color: white;
          font-size: 1rem;
          cursor: pointer;
          border-radius: 5%; }
          .post-template .right .content .nick-with-delete-button .delete-button:hover {
            background: #e03131;
            font-size: 1.1rem; }
  @media (max-width: 1023px) {
    .post-template {
      flex-direction: column;
      justify-content: right; }
      .post-template .pic {
        width: 100%;
        height: auto; } }

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 500px); }
  @media (max-width: 679px) {
    .loading {
      height: calc(100vh - 500px); } }

.hashed {
  color: #1c7ed6;
  font-weight: 800;
  cursor: pointer;
  margin-left: 5px;
  margin-right: 5px; }
  .hashed:hover {
    color: #1864ab; }

.word {
  margin-left: 5px;
  margin-right: 5px; }

.test {
  display: flex;
  flex-wrap: wrap; }
