.each-feed {
  margin-top: 20px;
  margin-bottom: 100px; }
  @media (max-width: 679px) {
    .each-feed {
      margin-left: 0;
      margin-right: 0;
      margin-top: -20px; } }
  .each-feed .top {
    font-size: 1.5rem;
    padding-left: 15px;
    display: flex;
    background: white;
    box-sizing: content-box;
    box-sizing: initial;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 10px;
    padding-top: 10px;
    border-radius: 20px 20px 0 0;
    justify-content: space-between;
    align-items: center;
    width: 690px; }
    @media (max-width: 1023px) {
      .each-feed .top {
        width: 100%;
        font-size: 1.25rem; } }
  @media (max-width: 1023px) and (max-width: 499px) {
    .each-feed .top {
      font-size: 1rem; } }
    .each-feed .top .left {
      min-width: 115px;
      width: 350px;
      display: flex;
      align-items: center; }
      .each-feed .top .left .profile-pic {
        background: #dee2e6;
        width: 80px;
        height: 80px;
        border-radius: 50%;
        margin-right: 10px; }
        @media (max-width: 1023px) {
          .each-feed .top .left .profile-pic {
            width: 50px;
            height: 50px; } }
        .each-feed .top .left .profile-pic img {
          border-radius: 50%;
          width: 100%;
          height: 100%;
          object-fit: contain; }
    .each-feed .top .right {
      width: 200px;
      margin-right: 15px; }
  .each-feed .img-area {
    width: 750px;
    height: auto; }
    .each-feed .img-area .onimg {
      cursor: pointer; }
    @media (max-width: 1023px) {
      .each-feed .img-area {
        width: 100%; } }
    .each-feed .img-area img {
      width: 100%;
      object-fit: contain;
      margin-bottom: -5px; }
    .each-feed .img-area .bottom {
      background: white;
      border-radius: 0 0 20px 20px; }
      .each-feed .img-area .bottom .comment-area {
        font-size: 1.5rem;
        display: flex;
        width: 20px;
        justify-content: center; }
        .each-feed .img-area .bottom .comment-area .cCount {
          display: flex; }
      .each-feed .img-area .bottom .test {
        font-size: 1.5rem;
        padding-top: 10px;
        padding-left: 30px;
        padding-right: 30px;
        padding-bottom: 20px;
        border-top: 2px solid #adb5bd; }
        @media (max-width: 679px) {
          .each-feed .img-area .bottom .test {
            width: 100%;
            font-size: 1.25rem; } }
