.comment {
  height: 167%;
  background: #ECEFEE;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around; }
  @media (max-width: 1023px) {
    .comment {
      height: auto;
      width: 100%; } }
  .comment .comments-list {
    height: 80%;
    overflow: auto;
    overflow-x: auto;
    overflow-y: auto;
    width: 420px;
    background: white;
    margin-bottom: 10px;
    margin-top: 10px;
    border-radius: 5%;
    box-sizing: border-box;
    padding: 20px; }
    @media (max-width: 1023px) {
      .comment .comments-list {
        height: auto;
        width: 100%; } }
  .comment .comment-write {
    display: flex;
    width: 100%;
    justify-content: space-around; }
    .comment .comment-write .comment-input {
      font-size: 1.3rem;
      font-weight: 400;
      border: none;
      outline: none;
      border-radius: 5%;
      height: 30px;
      width: 76%;
      padding-left: 15px;
      padding-left: 15px; }
    .comment .comment-write .comment-submit {
      font-size: 0.9rem;
      font-weight: 700;
      outline: none;
      border: none;
      background: #339af0;
      cursor: pointer;
      border-radius: 5%;
      color: white;
      width: 50px;
      height: 30px; }
      .comment .comment-write .comment-submit:hover {
        background: #1c7ed6; }
  .comment .show-likers {
    cursor: pointer; }
    .comment .show-likers:hover {
      color: #868e96; }

::-webkit-scrollbar {
  width: 7px; }

::-webkit-scrollbar-track {
  background: #f1f1f1; }

::-webkit-scrollbar-thumb {
  background: #888; }

::-webkit-scrollbar-thumb:hover {
  background: #555; }
