.register-form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: calc(100vh - 300px);
  background: white;
  width: 90%;
  margin-top: 13vh;
  box-shadow: -60px 0px 100px -90px #000000, 60px 0px 100px -90px #000000; }
  @media (max-width: 1023px) {
    .register-form {
      height: calc(100vh - 60px);
      margin: 0;
      width: 100%; } }
  .register-form .checkSyntax {
    font-size: 1.25rem;
    font-weight: 600; }
  .register-form .checkButton {
    margin-bottom: 40px;
    font-size: 1rem;
    border: none;
    background: #339af0;
    width: 140px;
    height: 30px;
    padding: 7px;
    border-radius: 5%;
    cursor: pointer;
    color: white; }
    .register-form .checkButton:hover {
      background: #1c7ed6;
      -webkit-transition: 0.3s;
      transition: 0.3s; }
  .register-form .submit-regi {
    color: white;
    background: #339af0;
    cursor: pointer;
    border: none;
    outline: none;
    margin-top: 30px;
    font-size: 1.5rem;
    width: 200px;
    height: 50px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23); }
    .register-form .submit-regi:hover {
      background: #1c7ed6;
      -webkit-transition: 0.3s;
      transition: 0.3s; }

.input {
  margin-bottom: 10px;
  text-align: center;
  width: 80%;
  height: 30px;
  outline: none;
  border: none;
  border-bottom: 1px solid #343a40;
  font-size: 1.5rem; }
