.login {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: calc(100vh - 120px);
  flex-wrap: wrap; }
  .login .inner {
    background: white;
    width: 340px;
    height: 600px;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 3.5%; }
    @media (max-width: 679px) {
      .login .inner {
        height: calc(100vh - 60px);
        width: 100%;
        margin-top: 65px;
        justify-content: flex-start;
        margin-left: 15px;
        margin-right: 15px; } }
    .login .inner .error-message {
      color: red;
      font-weight: 800;
      font-size: 1rem;
      text-align: center; }
  .login .input {
    margin-bottom: 5px;
    text-align: center;
    width: 290px;
    height: 30px;
    outline: none;
    border: none;
    border-bottom: 1px solid #343a40;
    font-size: 1.5rem; }
    @media (max-width: 679px) {
      .login .input {
        width: 250px; } }
  .login .submit {
    font-size: 1.5rem;
    width: 290px;
    height: 50px;
    background: #4dabf7;
    border-radius: 10px;
    border: none;
    color: white;
    text-align: center;
    cursor: pointer; }
    .login .submit:hover {
      background: #228be6;
      -webkit-transition: 0.5s;
      transition: 0.5s; }
    @media (max-width: 679px) {
      .login .submit {
        width: 250px; } }
  .login .left-box {
    background: white;
    width: 340px;
    height: 600px;
    box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-left: 5px;
    margin-top: 3.5%; }
    @media (max-width: 679px) {
      .login .left-box {
        display: none; } }
  .login .img {
    width: 100%;
    height: 100%; }
