.posts {
  display: grid;
  justify-content: center;
  grid-auto-rows: 200px;
  grid-template-columns: 200px 200px repeat(auto-fit, 200px);
  grid-gap: 20px;
  margin-bottom: 20px;
  cursor: pointer;
  padding-left: 25%;
  padding-right: 25%;
  padding-top: 1%; }
  @media (max-width: 679px) {
    .posts {
      grid-auto-rows: 170px;
      grid-template-columns: 170px 170px repeat(auto-fit, 170px); } }
  .posts img {
    border-radius: 20%;
    box-shadow: 5px 5px 39px -9px #000000;
    object-fit: contain; }
    .posts img:first-of-type {
      grid-column: 1 /3;
      grid-row: 1/ 3; }
  @media (max-width: 679px) {
    .posts {
      grid-template-columns: 170px 170px; } }

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 500px); }
  @media (max-width: 679px) {
    .loading {
      height: calc(100vh - 500px); } }

.no-post {
  font-size: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50vh;
  font-weight: 900; }
